@import './../../common/styles/vars';

section {
  width: 100%;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 5px $dark-color;
  color: $dark-color;
}

li {
  margin: 10px 0;
  border-radius: 5px;
  box-shadow: 0 0 3px $dark-color;
  padding: 10px;
  position: relative;

  div + div {
    margin-top: 5px;
  }

  div:first-child span {
    text-transform: lowercase;
  }

  input:checked {
    pointer-events: none;
    opacity: 0.5;
  }

  input:checked ~ span {
    color: grey;
    text-decoration: line-through;
  }

  div:nth-child(2) {
    display: flex;
    justify-content: space-between;
    color: grey;

    span {
      font-variant: small-caps;
    }
  }

  div:nth-child(3) {
    color: grey;
    // position: absolute;

    span {
      font-variant: small-caps;
    }
  }
}

.unvisible {
  visibility: hidden;
}

.immediatly {
  color: crimson;
}
